body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* navbar style */
.navbar {
  width: 100%;
  background: #2f96a3;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.navbar h2 {
  color: whitesmoke;
  font-weight: bolder;
  text-align: center;
  margin-top: 10px;
}

.navbar .fas {
  color: whitesmoke;
  font-size: 30px;
  margin: 5px;
}

/* weather container for whole page */
.weather-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 10px;
}

.weather-container .map-content {
  width: 40%;
}

.weather-container .current-temperatures {
  width: 30%;
}

.weather-container .long-temperatures {
  width: 30%;
}

@media screen and (max-width: 1200px) {
  .weather-container {
    flex-wrap: wrap;
  }
  .weather-container .map-content {
    width: 50%;
    margin-right: 2%;
  }
  .weather-container .current-temperatures {
    width: 40%;
    margin-right: 2%;
  }
  .weather-container .long-temperature {
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
  }
}

@media screen and (max-width: 768px) {
  .weather-container .map-content,
  .weather-container .current-temperatures,
  .weather-container .long-temperature {
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
  }
}
/*# sourceMappingURL=forecast.css.map */
.map-content {
  display: block;
  margin-top: 15px;
  position: relative;
  background: -webkit-gradient(linear, left top, left bottom, from(#2f96a3), to(#303e8f));
  background: linear-gradient(#2f96a3, #303e8f);
}

.map-content li {
  list-style-type: none;
}

.map-content .img-pos {
  width: 90%;
  margin-left: 5%;
  margin-right: 5%;
}

.map-content #pirot {
  bottom: 22%;
  right: 10%;
}

.map-content #nis {
  bottom: 27%;
  right: 25%;
}

.map-content #kragujevac {
  bottom: 40%;
  right: 37%;
}

.map-content #uzice {
  bottom: 38%;
  left: 20%;
}

.map-content #zajecar {
  bottom: 37%;
  right: 20%;
}

.map-content #pozarevac {
  top: 35%;
  right: 35%;
}

.map-content #beograd {
  top: 30%;
  left: 29%;
}

.map-content #novi-sad {
  top: 20%;
  left: 25%;
}

.map-content #subotica {
  top: 5%;
  left: 20%;
}

.map-content .city-pos {
  position: absolute;
  cursor: pointer;
  text-shadow: 2px 2px 4px whitesmoke;
  font-weight: bolder;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.map-content .city-pos .temperature-content {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.map-content .city-pos .temperature-content img {
  margin: 2px;
  padding: -5px;
  height: 30px;
  width: 30px;
  background: grey;
}

.map-content .city-pos .temperature-content h2,
.map-content .city-pos .temperature-content span {
  margin: 2px;
}

.map-content .city-pos:hover {
  text-shadow: 2px 2px 4px;
}
/*# sourceMappingURL=CurrentTemperature.css.map */
.current-temperatures {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.current-temperatures .currDate {
  text-align: center;
  color: red;
  font-weight: bolder;
}

.current-temperatures .curr-city {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 2px;
}

.current-temperatures .curr-city .city-name {
  width: 45%;
  margin-left: 10px;
  margin-top: 2px;
  font-weight: bolder;
}

.current-temperatures .curr-city .city-name .fa {
  cursor: pointer;
  padding-left: 10px;
}

.current-temperatures .curr-city .city-name .fa.fa:hover {
  font-size: 120%;
  font-weight: bolder;
}

.current-temperatures .curr-city #temp-icon {
  margin: 2px;
  height: 45px;
  width: 45px;
  margin-top: 2px;
  background: grey;
}

.current-temperatures .curr-city .minTemp {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 10%;
}

.current-temperatures .curr-city .minTemp #min {
  color: green;
  font-size: 16px;
  padding-top: 5px;
  padding-right: 10px;
}

.current-temperatures .curr-city .minTemp #text {
  font-size: 10px;
  font-weight: bolder;
  padding-top: 5px;
  padding-right: 10px;
}

.current-temperatures .curr-city .maxTemp {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 15%;
}

.current-temperatures .curr-city .maxTemp #max {
  color: red;
  font-size: 25px;
  padding-left: 5px;
}

.current-temperatures .curr-city .maxTemp #text {
  font-size: 10px;
  font-weight: bolder;
  padding-left: 5px;
}

@media screen and (min-width: 768px) {
  .current-temperatures .curr-city .maxTemp {
    width: 20%;
  }
  .current-temperatures .curr-city .minTemp {
    width: 15%;
  }
  .current-temperatures .curr-city .minTemp #min {
    padding-top: 7px;
  }
}
/*# sourceMappingURL=DailyTemperature.css.map */
.long-temperature {
  display: block;
  width: 30%;
  margin-top: 10px;
}

.long-temperature .long-temp {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.long-temperature .long-temp .dayFirst,
.long-temperature .long-temp .daySecond,
.long-temperature .long-temp .dayThird {
  width: 30%;
  text-align: center;
  color: red;
}
